@import "../../../assets/scss/common.scss";

.unitNaming {
  padding: 5px;

  .tabIndicator {
    background-color: #8248cb !important;
    height: 4px;
    border-radius: 400px;
  }

  .tabRoot {
    @extend .B2-Semi-bold;
    color: #787c7e;
    text-transform: capitalize;
  }

  .selectedTab {
    @extend .tabRoot;
    color: #1e2528 !important;
  }

  .contentPart {
    padding: 10px;
    margin: auto;
    height: 65vh;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;


  }

  hr {
    border-top: 2px solid #E8E9E9;
  }

  .btn {
    column-gap: 17px;
  }

  .rowWidth {
    width: 270px;
  }

  .AddressWidth {
    width: 540px;
  }

  .btnWidth {
    width: 160px;
  }

  .propertyLabel {
    color: #090909;
  }

  .basicForm {
    background: #FAFBFB;
    border-radius: 16px;
    margin: 15px;
    padding: 10px;
  }

  .basicForm2 {
    // background: #FAFAFA;
    border-radius: 16px;
  }

  .gap {
    gap: 10px;
  }

  .tabwrap {
    max-width: 368px;
    margin-top: 24px;
    // background: #ffffff;

  }

  .room_package_tab {
    display: flex;
    padding: 4px 6px;
    background-color: #ffffff;


  }

  .title {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    font-family: "Inter";
    color: #090909;
    margin-bottom: 16px;
  }

  .customfile {
    border: 1.5px solid #e8e9e9;
    border-radius: 16px;

    .label {
      // height: 90px;
      width: 100%;
      padding: 0 15px;
      display: flex;
      gap: 17px;
      position: relative;
      cursor: pointer;

      .uploadtext {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        font-family: "Barlow";
        color: #4b5153;
      }

      .uploadbtn {
        font-size: 14px !important;
        font-weight: 500 !important;
        line-height: 20px;
        font-family: "Barlow";
        padding: 11px 26px;

        position: absolute;
        z-index: 2 !important;
        right: 24px;
        top: 0;
        bottom: 0;
        margin: auto;
        border-radius: 10px;
        color: #935ed5;
        background-color: #fff;
        display: inline-table;
        text-decoration: none;
      }

      .downloadbtn {
        font-size: 14px !important;
        font-weight: 500 !important;
        line-height: 20px;
        font-family: "Barlow";
        padding: 11px 26px;

        position: absolute;
        z-index: 2 !important;
        right: 24px;
        top: 0;
        bottom: 0;
        margin: auto;
        border-radius: 10px;
        color: #935ed5;
        display: inline-table;
        text-decoration: none;
      }

      .customfile {
        border: 1.5px solid #e8e9e9;
        border-radius: 16px;

        .label {
